import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Typography,
  Paper,
  ListItem,
  Grid
} from "@material-ui/core";

import cls from "./styles";

function SkillSet(props) {
  const { details, classes } = props;
  return (
    <Paper className={classes.content}>
      <Typography variant="h6">{details.type}</Typography>
      <Grid className={classes.contains}>
        {details.dev ?
          <React.Fragment>
            <Typography style={{ color: "#999999" }}>Development</Typography>
            {details.dev.map((rec,ind) => {
              return <ListItem key={ind} button>{rec}</ListItem>

            })}
          </React.Fragment> : null}
        {details.test ?
          <React.Fragment>
            <Typography style={{ color: "#999999" }}>Unit Testing</Typography>
            {details.test.map((rec,ind) => {
              return <ListItem key={ind} button>{rec}</ListItem>

            })}
          </React.Fragment> : null}
        {details.values ?
          <React.Fragment>
            {details.values.map((rec, ind) => {
              return <ListItem key={ind} button>{rec}</ListItem>

            })}
          </React.Fragment> : null}
      </Grid>
    </Paper>
  );
}

export default withStyles(cls)(SkillSet);