import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Divider,
  Typography,
  Paper,
  Grid
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import TimelineIcon from '@material-ui/icons/Timeline';

import cls from "./styles";
import Icon from "./../favicon";
import util from "./../../utils";

function Experience(props) {
  const { details, classes } = props;;
  const logo = util.getLogo(details.logo);
  return (
    <Paper className={classes.content}>
      <Grid container>
        <Grid item xs={8}>
          <Typography variant="h6">{details.org}</Typography>
          <Typography className={classes.type}>{details.type}</Typography>
          <Typography className={classes.role}>ROLE</Typography>
          <Typography>{details.role}</Typography>
          <Typography className={classes.location}><LocationOnIcon />{details.location}</Typography>
          <Typography className={classes.type}><TimelineIcon />{details.duration}</Typography>
        </Grid>
        <Grid item md={4} className={classes.logo}>
          <img src={logo} alt="logo" width="80" height="80" />
        </Grid>
        <Divider className={classes.divider} />
       <Grid item md={2}><Icon /></Grid>
       <Grid item md={10}><Typography className={classes.comment}>{details.comment}</Typography></Grid>
      </Grid>
    </Paper>
  );
}

export default withStyles(cls)(Experience);