import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Grid, IconButton
} from "@material-ui/core";
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import FacebookIcon from '@material-ui/icons/Facebook';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';

import cls from "./styles";
import CONSTANT from "./../../utils/constants";

function App(props) {
 const social = CONSTANT.USER.social;
 const {classes, style}=props;
  return (
    <Grid container className={classes.social} style={style}>
      <IconButton href={social.linkedin} title="LinkedIn" target="_blank">
        <LinkedInIcon fontSize="large" className={classes.icons}/>
      </IconButton >
      <IconButton href={social.git} title="Git Hub" target="_blank">
        <GitHubIcon fontSize="large" className={classes.icons}/>
      </IconButton>
      <IconButton href={social.facebook} title="Facebook" target="_blank">
        <FacebookIcon fontSize="large" className={classes.icons}/>
      </IconButton>
      <IconButton onClick={()=>alert(`Please whatsApp me at ${social.whatApp}`)} title="What's App">
        <WhatsAppIcon fontSize="large" className={classes.icons}/>
      </IconButton>
    </Grid>
  );
}

export default withStyles(cls)(App);