import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Typography,
  Paper
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';

import cls from "./styles";
import utils from "./../../utils";

function Hobbies(props) {
  const {details, classes}=props;
  const logo=utils.getLogo(details.logo);
  return (
      <Paper className={classes.content}>
        <Typography variant="h6" className={classes.title}>{details.title}</Typography>
        <Typography className={classes.type}>{details.type}</Typography>
          <Typography className={classes.org}>
          <img src={logo} alt="logo" width="14" style={{ margin: "0 2px 0 5px" }} />
          {details.org}
        </Typography>
        <Typography className={classes.location}><LocationOnIcon />{details.location}</Typography>
      </Paper>
  );
}

export default withStyles(cls)(Hobbies);