import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Divider,
  Typography,
  Paper,
  Grid
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';

import classes from "./styles";
import Icon from "./../favicon";
import utils from "./../../utils";

function Project(props) {
  const { details, classes } = props;
  const logo = utils.getLogo(details.logo);
  return (
    <Paper className={classes.content}>
      <Typography style={{ color: "#999999" }}>#{details.count}</Typography>
      <Typography variant="h6">{details.title}</Typography>
      <Typography className={classes.org}><img src={logo} alt="logo" width="14" style={{ margin: "0 2px 0 5px" }} />{details.org}</Typography>
      <Typography className={classes.location}><LocationOnIcon />{details.location}</Typography>
      <Divider className={classes.divider} />
      <Grid container>
        <Grid item xs={12} md={2}>
          <Icon />
        </Grid>
        <Grid item xs={12} md={10}>
          <Typography className={classes.comment}>{details.description}</Typography>
        </Grid>
      </Grid>
    </Paper>
  );
}

export default withStyles(classes)(Project);