/* eslint-disable react/no-typos */
import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from "@material-ui/core/styles";
import {
    Grid, Typography
} from "@material-ui/core";
import Typed from 'typed.js';

import cls from "./styles";
import SocialMedia from "./../../components/socialLink";
import CONSTANT from "./../../utils/constants";

class Header extends React.PureComponent {
    componentDidMount() {
        new Typed('#type', {
            strings: CONSTANT.USER.type,
            typeSpeed: 40,
            loop: true
        });
    }
    render() {
        const { classes } = this.props;
        const user = CONSTANT.USER;
        return <Grid container className={classes.banner}>
            <Grid item xs={12} md={6} className={classes.details}>
                <Typography component="h1">
                    {user.name}
                </Typography>
                <Typography component="h5">
                    {user.role}
                </Typography>
                <Typography component="h6">
                    {user.company}
                </Typography>
                <Typography component="h5">
                    {`I `}
                    <span id="type"></span>
                </Typography>
                <SocialMedia style={{justifyContent: "flex-start"}}/>
            </Grid>
        </Grid>
    }
}
Navigator.propTypes = {
    click: PropTypes.func
}

export default withStyles(cls)(Header);