import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Typography,
  Paper,
  Grid
} from "@material-ui/core";

import cls from "./styles";

function Intro(props) {
  const {details, classes}=props;
  return (
    <Grid container className={classes.grid}>
    <Grid item xs={12} md={4} style={{ padding: "8px" }}>
        <Paper style={{ padding: "10px" }}>
            <Typography className={classes.title}>Why Programming?</Typography>
            {details.whyProgram.map((rec,ind)=>{
              return <Typography key={ind} className={classes.para }>{rec}</Typography>
            })}
        </Paper>
    </Grid>
    <Grid item xs={12} md={4} style={{ padding: "8px" }}>
        <Paper style={{ padding: "10px" }}>
            <Typography className={classes.title} >My Role @ {details.org}</Typography>
            {details.roleAt.map((rec,ind)=>{
              return <Typography key={ind} className={classes.para }>{rec}</Typography>
            })}
        </Paper>
    </Grid>
</Grid>
  );
}

export default withStyles(cls)(Intro);