/* eslint-disable react/no-typos */
import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from "@material-ui/core/styles";
import {
  Grid,
  AppBar,
  Toolbar,
  Button
} from "@material-ui/core";

import FeviIcon from "./../favicon";
import cls from "./styles";
import CONSTANT from "./../../utils/constants";

class Navigator extends React.PureComponent {

  render() {
    const { classes, click } = this.props;
    return <Grid container>
      <AppBar position="fixed" className={classes.navigator}>
        <Toolbar>
          <FeviIcon />
          {CONSTANT.NAV_OPTIONS.map(name => {
            return <Button key={name} color="default" onClick={() => click(name)}>{name}</Button>
          })}
        </Toolbar>
      </AppBar>
    </Grid>
  }
}
Navigator.propTypes = {
  click: PropTypes.func
}

export default withStyles(cls)(Navigator);