
import walmart from "./../images/walmart-logo.png";
import ps from "./../images/ps-logo.svg";
import infy from "./../images/infy_logo.png";
import brillio from "./../images/brillio_logo.jpeg";
import ms from "./../images/ms_logo.png";

export default {
    getLogo: (name) => {
        let logo = null;
        if (name === "infy") {
            logo = infy;
        } else if (name === "brillio") {
            logo = brillio;
        } else if (name === "ps") {
            logo = ps;
        }
        else if (name === "walmart") {
            logo = walmart;
        }
        else if (name === "ms") {
            logo = ms;
        }
        return logo;
    }
}