import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Grid,
  Button,
  Typography,
  withWidth
} from "@material-ui/core";

import cls from "./styles";
import Social from "./../socialLink";
import CONSTANT from "./../../utils/constants";


function App(props) {
  const { classes, width } = props;
  const center = width === "xs" || width === "sm";
  return (
    <Grid container className={classes.footer} id="CONTACT">
      <Grid item xs={12} md={3} className={classes.img}></Grid>
      <Grid item xs={12} md={5} className={classes.text}>
        <Typography variant="h4">Let's make the world a better place</Typography>
        <Typography variant="h4"> Get in touch</Typography>
        <Grid item xs={12} md={5}>
          <Button
          className={classes.linkedIn}
          title="Connect on LinkedIn"
          target="_blank"
          href={CONSTANT.USER.social.linkedin}> Connect on LinkedIn </Button>
        </Grid>
        <Grid item xs={12} md={5}>
          <Button
          className={classes.cv}
          title="Request Resume/CV"
          href={`mailto:${CONSTANT.USER.social.email}?cc=&subject=👨‍💻Resume%20request&body=Can%20you%20please%20share%20your%20full%20CV%20with%20me%20in%20word/pdf%20format?`}> Request Resume/CV</Button></Grid>
        <Grid item xs={12}><Social style={center ? null : { justifyContent: "flex-start" }} /></Grid>
      </Grid>
    </Grid>
  );
}

export default withStyles(cls)(withWidth()(App));