import React from 'react';
import { withStyles } from "@material-ui/core/styles";
import {
  Typography,
  Paper
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';

import cls from "./styles";

function Achievement(props) {
  const {details, classes}=props;
  return (
      <Paper className={classes.content}>
        <Typography variant="h6" className={classes.title}>{details.name}</Typography>
        <Typography className={classes.specilization}>{details.specilization}</Typography>
        <Typography className={classes.year}>{details.year}</Typography>
        <Typography className={classes.school}>🏛 {details.schoolName}</Typography>
        <Typography className={classes.year}><LocationOnIcon />{details.location}</Typography>
        <Typography className={classes.school}><span role="img" aria-label="img">🎓Score :</span> {details.score}</Typography>
      </Paper>
  );
}

export default withStyles(cls)(Achievement);